<template><section><h1>输入框 Input</h1>
<h3>基础用法</h3>
<p>通过鼠标或键盘输入字符</p>
<demo-block>
                            <div class="source" slot="source"><as-row>
  <as-input v-model="input" placeholder="请输入内容"></as-input>
</as-row>


</div>
                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">as-row</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">input</span>: <span class="hljs-string">''</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>禁用状态</h3>
<p>通过 <code v-pre="">disabled</code> 属性指定是否禁用 input 组件</p>
<demo-block>
                            <div class="source" slot="source"><as-input placeholder="请输入内容" v-model="input1" disabled></as-input>


</div>
                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">input1</span>: <span class="hljs-string">''</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>可清空</h3>
<p>使用<code v-pre="">clearable</code>属性即可得到一个可清空的输入框</p>
<demo-block>
                            <div class="source" slot="source"><as-input placeholder="请输入内容" v-model="input10" clearable=""></as-input>


</div>
                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input10"</span> <span class="hljs-attr">clearable</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">input10</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>带 icon 的输入框</h3>
<p>带有图标标记输入类型,可以通过 <code v-pre="">prefix-icon</code> 和 <code v-pre="">suffix-icon</code> 属性在 input 组件首部和尾部增加显示图标，也可以通过 slot 来放置图标。</p>
<demo-block>
                            <div class="source" slot="source">属性方式：
  <as-row :offset="20">
    <as-col :col="3">
      <as-input placeholder="请选择日期" suffix-icon="date" v-model="input2"></as-input>
    </as-col>
    <as-col :col="3">
      <as-input placeholder="请输入内容" prefix-icon="search" v-model="input21"></as-input>
    </as-col>
  </as-row>
  slot 方式：
  <as-row :offset="20">
    <as-col :col="3">
      <as-input placeholder="请选择日期" v-model="input22">
        <i slot="suffix" class="as-icon as-input__icon icon-date"></i>
      </as-input>
    </as-col>
    <as-col :col="3">
      <as-input placeholder="请输入内容" v-model="input23">
        <i slot="prefix" class="as-icon as-input__icon icon-search"></i>
      </as-input>
    </as-col>
  </as-row>

</div>
                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">属性方式：
  <span class="hljs-tag">&lt;<span class="hljs-name">as-row</span> <span class="hljs-attr">:offset</span>=<span class="hljs-string">"20"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-col</span> <span class="hljs-attr">:col</span>=<span class="hljs-string">"3"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择日期"</span> <span class="hljs-attr">suffix-icon</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-col</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-col</span> <span class="hljs-attr">:col</span>=<span class="hljs-string">"3"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">prefix-icon</span>=<span class="hljs-string">"search"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input21"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-col</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>
  slot 方式：
  <span class="hljs-tag">&lt;<span class="hljs-name">as-row</span> <span class="hljs-attr">:offset</span>=<span class="hljs-string">"20"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-col</span> <span class="hljs-attr">:col</span>=<span class="hljs-string">"3"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择日期"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input22"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"suffix"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"as-icon as-input__icon icon-date"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-col</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-col</span> <span class="hljs-attr">:col</span>=<span class="hljs-string">"3"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input23"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prefix"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"as-icon as-input__icon icon-search"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-col</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">input2</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">input21</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">input22</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">input23</span>: <span class="hljs-string">''</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>文本域</h3>
<p>用于输入多行文本信息，通过将 <code v-pre="">type</code> 属性的值指定为 textarea, 文本域高度可通过 <code v-pre="">rows</code> 属性控制</p>
<demo-block>
                            <div class="source" slot="source"><as-input type="textarea" :rows="2" placeholder="请输入内容" v-model="textarea"></as-input>


</div>
                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">:rows</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"textarea"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">textarea</span>: <span class="hljs-string">''</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>可自适应文本高度的文本域</h3>
<p>通过设置 <code v-pre="">autosize</code> 属性可以使得文本域的高度能够根据文本内容自动进行调整，并且 <code v-pre="">autosize</code> 还可以设定为一个对象，指定最小行数和最大行数。</p>
<demo-block>
                            <div class="source" slot="source"><as-row>
  <as-input type="textarea" autosize="" placeholder="请输入内容" v-model="textarea2"></as-input>
</as-row>
<as-row>
  <as-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入内容" v-model="textarea3"></as-input>
</as-row>


</div>
                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">as-row</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">autosize</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"textarea2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">as-row</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"textarea"</span> <span class="hljs-attr">:autosize</span>=<span class="hljs-string">"{ minRows: 2, maxRows: 4}"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"textarea3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">textarea2</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">textarea3</span>: <span class="hljs-string">''</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>复合型输入框</h3>
<p>可前置或后置元素，一般为标签或按钮, 可通过 slot 来指定在 input 中前置或者后置内容。</p>
<demo-block>
                            <div class="source" slot="source"><as-row>
    <as-input placeholder="请输入内容" v-model="input3">
      <template slot="prepend">Http://</template>
    </as-input>
  </as-row>
  <as-row>
    <as-input placeholder="请输入内容" v-model="input2">
      <template slot="append">.com</template>
    </as-input>
  </as-row>
  <as-row>
    <as-input placeholder="请输入内容" v-model="input2">
      <as-select v-model="select1" slot="prepend" placeholder="请选择">
        <as-select-option value="1" text="IT 男"></as-select-option>
        <as-select-option value="2" text="NN Boyy"></as-select-option>
        <as-select-option value="3" text="FF Jike"></as-select-option>
      </as-select>
      <as-button slot="append" icon="search"></as-button>
    </as-input>
  </as-row>

</div>
                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">as-row</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input3"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prepend"</span>&gt;</span>Http://<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">as-row</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input2"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"append"</span>&gt;</span>.com<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">as-row</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input2"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">as-select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"select1"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prepend"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">as-select-option</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">text</span>=<span class="hljs-string">"IT 男"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-select-option</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">as-select-option</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">text</span>=<span class="hljs-string">"NN Boyy"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-select-option</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">as-select-option</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">text</span>=<span class="hljs-string">"FF Jike"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-select-option</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">as-select</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">as-button</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"append"</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"search"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">as-row</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">input2</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">input3</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">select1</span>: <span class="hljs-string">''</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>尺寸</h3>
<p>可通过 <code v-pre="">size</code> 属性指定输入框的尺寸，除了默认的大小外，还提供了 lg、md(默认)、sm 和 xs 三种尺寸。</p>
<demo-block>
                            <div class="source" slot="source"><as-row :offset="20">
    <as-col :col="2">
      <as-input size="lg" placeholder="请输入内容" suffix-icon="date" v-model="input2"></as-input>
    </as-col>
    <as-col :col="2">
      <as-input size="md" placeholder="请输入内容" suffix-icon="date" v-model="input2"></as-input>
    </as-col>
    <as-col :col="2">
      <as-input size="sm" placeholder="请输入内容" suffix-icon="date" v-model="input2"></as-input>
    </as-col>
    <as-col :col="2">
      <as-input size="xs" placeholder="请输入内容" suffix-icon="date" v-model="input2"></as-input>
    </as-col>
  </as-row>
</div>



                            <h1></h1>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">as-row</span> <span class="hljs-attr">:offset</span>=<span class="hljs-string">"20"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-col</span> <span class="hljs-attr">:col</span>=<span class="hljs-string">"2"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"lg"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">suffix-icon</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-col</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-col</span> <span class="hljs-attr">:col</span>=<span class="hljs-string">"2"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"md"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">suffix-icon</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-col</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-col</span> <span class="hljs-attr">:col</span>=<span class="hljs-string">"2"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"sm"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">suffix-icon</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-col</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">as-col</span> <span class="hljs-attr">:col</span>=<span class="hljs-string">"2"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">as-input</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"xs"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span> <span class="hljs-attr">suffix-icon</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">as-input</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">as-col</span>&gt;</span>
